	import {
		cartItemClear,
		cartItemDel,
		cartItemList,
		cartItemUpdateNum,
		cartItemUpdateSku,
		productDetail
	} from '@/api/product';
	export default {
		data() {
			return {
				sumPrice: '0.00',
				headerPosition: 'fixed',
				headerTop: null,
				statusTop: null,
				showHeader: true,
				selectedList: [],
				isAllselected: false,
				//控制滑动效果
				theIndex: null,
				oldIndex: null,
				isStop: false,
				cartList: [],
				hasLogin: null,
				oldDiscount: 0,
				footerbottom: 0,
				specClass: 'none',
				showTypeImage: null,
				productDetail: {},
				specSelected: [],
				specChildList: [],
				specList: [],
				currentStock: 0,
				currentSkuPrice: 0,
				cartCount: 0,
				currentSkuId: null,
				currentNewSkuId: null,
				styleObject: {},
        loading: true
			}
		},
		onPageScroll(e) {
			//兼容iOS端下拉时顶部漂移
			this.headerPosition = e.scrollTop >= 0 ? 'fixed' : 'absolute';
			this.headerTop = e.scrollTop >= 0 ? null : 0;
			this.statusTop = e.scrollTop >= 0 ? null : -this.statusHeight + 'px';
		},
		//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
		onPullDownRefresh() {
			if (!this.hasLogin) {
        if (type === 'refresh') {
          uni.stopPullDownRefresh();
        }
        return;
      };
			this.selectedList.length = 0;
			this.isAllselected = false;
			this.sumPrice = 0;
			this.getCartItemList('refresh');
		},
		onShow() {
			//兼容H5下结算条位置







			this.initData();
		},
		methods: {
			//规格弹窗开关
			toggleSpec(row) {
			  if (parseInt(row.status, 10) === 0) return;
				if (this.specClass === 'show') {
					if (!this.hasLogin) {
						this.specClass = 'none';
						this.$mHelper.toast('请先登录！');
						return;
					}
					if (this.specSelected.length < this.productDetail.base_attribute_format.length) {
						this.$mHelper.toast('请先选择规格');
						return;
					}
					this.handleCartItemUpdateSku(this.currentSkuId, this.currentNewSkuId);
					this.specClass = 'hide';
					setTimeout(() => {
						this.specClass = 'none';
					}, 250);
				} else if (this.specClass === 'none') {
					this.specClass = 'show';
					if (row) {
						this.specChildList = [];
						this.cartCount = row.number;
						this.getProductDetail(row);
					}
				}
			},
			//选择规格
			selectSpec(index, pid, type) {
				let list = this.specChildList;
				list.forEach(item => {
					if (item.base_spec_id === pid) {
						this.$set(item, 'selected', false);
					}
				});
				if (parseInt(type, 10) === 3) {
					this.showTypeImage = list[index].data;
				}
				if (parseInt(type, 10) === 2) {
					this.styleObject = {
						color: list[index].data
						// border: `1px solid ${list[index].data}`,
					};
				}
				this.$set(list[index], 'selected', true);
				//存储已选择
				/**
				 * 修复选择规格存储错误
				 * 将这几行代码替换即可
				 * 选择的规格存放在specSelected中
				 */
				this.specSelected = [];
				list.forEach(item => {
					if (item.selected === true) {
						this.specSelected.push(item);
					}
				})
				let skuStr = [];
				this.specSelected.forEach(item => {
					skuStr.push(item.base_spec_value_id)
				})
				this.productDetail.sku.forEach(item => {
					if (item.data === skuStr.join('-')) {
						this.currentStock = item.stock;
						this.currentSkuPrice = item.price;
						this.currentNewSkuId = item.id;
						return;
					}
				})
			},
			// 获取产品详情
			async getProductDetail(row) {
				this.currentSkuId = row.sku_id;
				this.currentNewSkuId = row.sku_id;
				await this.$http.post(`${productDetail}`, {
					keyName: row.id
				}).then(async r => {
					this.productDetail = r.data;
					this.specList = this.productDetail.base_attribute_format;
					this.specList.forEach(item => {
						this.specChildList = [...this.specChildList, ...item.value]
					});
					/**
					 * 修复选择规格存储错误
					 * 将这几行代码替换即可
					 * 选择的规格存放在specSelected中
					 */
					this.specSelected = [];
					this.specChildList.forEach(item => {
						if (row.sku_name.indexOf(item.title) !== -1) {
							item.selected = true;
							this.specSelected.push(item);
						}
					})

					let skuStr = [];
					this.specSelected.forEach(item => {
						skuStr.push(item.base_spec_value_id)
					});
					this.productDetail.sku.forEach(item => {
						if (item.data === skuStr.join('-')) {
							this.currentStock = item.stock;
							this.currentSkuPrice = item.price;
							return;
						}
					});
				});
			},
			hideSpec() {
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			},
			stopPrevent() {
			},
			// 删除选中购物车商品
			async deleteCartItem(id, type) {
				const sku_ids = []
				if (type) {
					sku_ids.push(parseInt(id, 10))
				} else {
					for (let i = 0; i < this.cartList.length; i++) {
						if (this.cartList[i].williamCartItem.checked) {
							sku_ids.push(this.cartList[i].williamCartItem.itemId);
						}
					}
				}
				console.log(sku_ids.join(","))
				await this.$http.post(`${cartItemDel}`, {
					keyName: sku_ids.join(",")
				}).then(() => {
					this.selectedList.length = 0;
					this.isAllselected = false;
					this.sumPrice = 0;
					this.getCartItemList();
					this.oldIndex = null;
					this.theIndex = null;
				});
			},
			// 修改购物车商品sku
			async handleCartItemUpdateSku(sku_id, new_sku_id) {
				await this.$http.post(`${cartItemUpdateSku}`, {
					sku_id,
					new_sku_id
				}).then(() => {
					this.selectedList.length = 0;
					this.isAllselected = false;
					this.sumPrice = 0;
					this.getCartItemList();
				});
			},
			// 数据初始化
			initData() {
				this.hasLogin = this.$mStore.getters.hasLogin;
				if (this.hasLogin) {
					this.theIndex = null;
					this.oldIndex = null;
					this.selectedList.length = 0;
					this.isAllselected = false;
					this.sumPrice = 0;
					this.getCartItemList();
				} else {
				  this.loading = false;
				  this.cartList.length = 0;
					this.selectedList.length = 0;
				}
			},
			// 通用跳转
			navTo(url) {
				uni.navigateTo({
					url
				})
			},
			// 获取购物车列表  TODO
			async getCartItemList(type) {
				await this.$http.post(`${cartItemList}`).then(r => {
					this.loading = false;
					if (type === 'refresh') {
						uni.stopPullDownRefresh();
					}
					this.cartList = r.data;
					let tempArr = [];
					// 获取选中列表
					r.data.forEach(function(item,index){
						if(item.williamCartItem.checked == 1){
							tempArr.push(item)
						}
					})
					this.selectedList = [...tempArr]
					uni.setStorageSync('cartNum', r.data.length)
					if (r.data.length === 0) {
						uni.removeTabBarBadge({index: 2});
						return;
					}
					uni.setTabBarBadge({
						index: 2,
						text: r.data.length.toString()
					});
				}).catch(() => {
				  this.loading = false;
				  this.hasLogin = false;
				  this.cartList.length = 0;
					if (type === 'refresh') {
						uni.stopPullDownRefresh();
					}
				})
			},
			// 清空购物车
			clearCart(params) {
				console.log(params)
			  const content = params ? '清空失效商品？' : '清空购物车？';
				uni.showModal({
					content,
					success: async (e) => {
						if (e.confirm) {
							await this.$http.post(`${cartItemClear}`, params).then(() => {
								this.selectedList.length = 0;
								this.isAllselected = false;
								this.sumPrice = 0;
								this.getCartItemList();
							});
						}
					}
				})
			},
			// 控制左滑删除效果
			touchStart(index, event) {
				//多点触控不触发
				if (event.touches.length > 1) {
					this.isStop = true;
					return;
				}
				this.oldIndex = this.theIndex;
				this.theIndex = null;
				//初始坐标
				this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
			},
			touchMove(index, event) {
				//多点触控不触发
				if (event.touches.length > 1) {
					this.isStop = true;
					return;
				}
				let moveX = event.touches[0].pageX - this.initXY[0];
				let moveY = event.touches[0].pageY - this.initXY[1];

				if (this.isStop || Math.abs(moveX) < 5) {
					return;
				}
				if (Math.abs(moveY) > Math.abs(moveX)) {
					// 竖向滑动-不触发左滑效果
					this.isStop = true;
					return;
				}

				if (moveX < 0) {
					this.theIndex = index;
					this.isStop = true;
				} else if (moveX > 0) {
					if (this.theIndex != null && this.oldIndex == this.theIndex) {
						this.oldIndex = index;
						this.theIndex = null;
						this.isStop = true;
						setTimeout(() => {
							this.oldIndex = null;
						}, 150)
					}
				}
			},
			touchEnd(index, $event) {
				//结束禁止触发效果
				this.isStop = false;
			},
			// 选中商品
			selected(index, item) {
			 if (parseInt(item.status, 10) === 0) return;
				this.cartList[index].selected = this.cartList[index].selected ? false : true;
				let i = this.selectedList.indexOf(this.cartList[index].williamCartItem.itemId);
				i > -1 ? this.selectedList.splice(i, 1) : this.selectedList.push(this.cartList[index].williamCartItem.itemId);
				this.isAllselected = this.selectedList.length == this.cartList.length;
				this.sum();
			},
			// 全选商品
			allSelect() {
				let len = this.cartList.length;
				let arr = [];
				for (let i = 0; i < len; i++) {
				  // 当商品
				  if(parseInt(this.cartList[i].status, 10) !== 0) {
            this.cartList[i].selected = this.isAllselected ? false : true;
            arr.push(this.cartList[i].id);
          }
				}
				this.selectedList = this.isAllselected ? [] : arr;
				this.isAllselected = this.isAllselected || arr.length == 0 ? false : true;
				if (arr.length > 0) {
				  this.sum();
        }
			},
			// 减少数量(执行接口)
			sub(item, index) {
				if (this.cartList[index].williamCartItem.goodsCnt <= 1) {
					return;
				}
				this.cartList[index].williamCartItem.goodsCnt--;
				this.numberChange(item);
			},
			// 增加数量(执行接口)
			add(item, index) {
				this.cartList[index].williamCartItem.goodsCnt++;
				this.numberChange(item, undefined, index, 'add');
			},
			// 控制可输入购物车商品数量
			discard() {
			},
			// 监听购物车商品数量改变
			async numberChange(item, data, index, type) {
				if (data) {
					this.cartList[index].number = data.detail.value;
				}
				await this.$http.post(`${cartItemUpdateNum}`, {
					itemId: item.williamCartItem.itemId,
					type: type == 'add' ? 1 : 2
				}).then(r => {
					if (r.code === 200) {
						this.sum();
					} else {
						if (type === 'add') {
							this.cartList[index].williamCartItem.goodsCnt--;
						}
						this.$mHelper.toast(r.message);
					}
				}).catch(() => {
					if (type === 'add') {
						this.cartList[index].williamCartItem.goodsCnt--;
					}
				})
			},
			// 创建订单
			async createOrder() {
				if (this.selectedList.length === 0) return;
				const data = {};
				const ids = [];
				let len = this.cartList.length;
				for (let i = 0; i < len; i++) {
					if (this.cartList[i].selected) {
						ids.push(parseInt(this.cartList[i].id, 10));
					}
				}
				data.type = 'cart';
				data.data = ids.join(',');
				this.selectedList.length = 0;
				this.isAllselected = false;
				this.sumPrice = 0;
				this.navTo(`/pages/order/create/order?data=${JSON.stringify(data)}`);
			},
			// 合计
			sum() {
				this.sumPrice = 0;
				let len = this.cartList.length;
				const arr = []
				for (let i = 0; i < len; i++) {
					if (this.cartList[i].selected) {
						arr.push(this.cartList[i]);
					}
				}
				console.log(arr)
				this.sumPrice = this.arrSort(arr);
				this.sumPrice = this.sumPrice.toFixed(2);
			},
			// 向下舍去小数点后两位
			floor(val) {
				return Math.floor(val * 100) / 100;
			},
